import React, { useState, useEffect } from "react";
import ".././components/Photos.css";

var url =
  "https://pixabay.com/api/?key=15039565-c2e62dfd6711de941df28667f&q=blue+flowers&image_type=photo&pretty=true";
function Photos() {
  const [photos, setphotos] = useState([]);
  const getPhotos = async () => {
    const resp = await fetch(url);
    const photos = await resp.json();
    setphotos(photos);
    console.log(photos);
  };
  useEffect(() => {
    getPhotos(url);
    return () => {};
  }, []);
  const searchImg = () => {
    var Search = document.querySelector("#inp").value;

    url = `https://pixabay.com/api/?key=15039565-c2e62dfd6711de941df28667f&q=" +
      ${Search} +
      "&image_type=photo&pretty=true`;
    console.log(url);
    getPhotos(url);
  };
  let itemsToRender;
  if (photos.hits) {
    itemsToRender = (
      <div id="mainbox">
        <div className="searchbox">
          <input id="inp" type="text" />
          <button id="btn" onClick={searchImg}>
            search
          </button>
        </div>
        {photos.hits.map((pic) => {
          const {
            id,
            downloads,
            largeImageURL,
            user,
            userImageURL,
            webformatURL,
          } = pic;
          return (
            <div key={id} className="box">
              <a href={largeImageURL}>
                <img src={webformatURL} alt={downloads} className="main" />
              </a>
              <div className=" txt">
                <img src={userImageURL} alt="" />
                <div className="usertext">
                  {user} <br />
                  {downloads} downloads
                </div>
              </div>
            </div>
          );
        })}
      </div>
    );
  } else {
    itemsToRender = "Loading...";
  }
  return <div>{itemsToRender}</div>;
}

export default Photos;
